.dashboard {
    h3 {
        font-size: 1.2em;
        border-bottom: 1px solid #ccc;
        margin-bottom: 10px;
        padding-bottom: 10px;
    }
    .left-menu {
        width: 18%;
        list-style: none;
        padding-right: 2%;
        border-right: 1px solid #ccc;
        margin-bottom: 30px;
        float: left;
        &>li {
            padding: 10px 0 20px;
            display: inline-block;
            border-bottom: 1px solid #ccc;
            width: 100%;
            font-size: 0.875em;
            color: $primary;
            &:last-child {
                border-bottom: none;
            }
            .sub-menu {
                list-style: none;
                padding-left: 1%;
                padding-top: 10px;
                font-size: 0.875em;
                &>li {
                    padding: 3px 0;
                    &.active {
                        a {
                            color: #0bc6a0;
                            font-weight: 900;
                            font-size: 1.1em;
                        }
                    }
                }
            }
        }
    }
    .dashboard-content {
        padding-left: 20%;
        .order-list {
            .order {
                width: 100%;
                margin: 20px 0 10px;
                background: #fff;
                border: 1px solid #e2e3e4;
                float: left;
                .order-head {
                    background: #f9f9f9;
                    border-bottom: 1px solid #e6e6e6;
                    padding: 12px 15px;
                    width: 100%;
                    float: left;
                    .order-number {
                        background: #f9f9f9;
                        border: 1px solid #d7d7d7;
                        padding: 5px 7px;
                        border-radius: 2px;
                        text-transform: uppercase;
                        cursor: pointer;
                        text-align: center;
                        float: left;
                        font-size: 0.875em;
                    }
                    .reciept {
                        cursor: pointer;
                        text-align: center;
                        border-radius: 2px;
                        background: #f9f9f9;
                        border: 1px solid #d7d7d7;
                        margin-left: 10px;
                        float: right;
                        padding: 5px 7px;
                        font-size: 0.875em;
                    }
                }
                .detail {
                    //@include boxShadow(0 1px 3px rgba(0, 0, 0, 0.28));
                    float: left;
                    width: 100%;
                    padding: 12px 15px;
                    .img {
                        width: 90px;
                        height: 65px;
                        float: left;
                        &>div {
                            width: 100%;
                            background-size: cover;
                            background-repeat: no-repeat;
                            height: 100%;
                        }
                    }
                    .desc {
                        padding-left: 7px;
                        float: left;
                        vertical-align: top;
                        width: calc(100% - 100px);
                        h4 {
                            font-size: 0.925em;
                            font-weight: 900;
                        }
                        .detail-desc {
                            width: 45%;
                            float: left;
                            color: #878787;
                        }
                        .amount-discount {
                            width: 12%;
                            margin-right: 3%;
                            float: left;
                            font-size: 0.775em;
                            text-align: right;
                            .cashback {
                                color: #28a745;
                            }
                        }
                        .status-section {
                            width: 40%;
                            float: left;
                            .msg {
                                margin-top: 3px;
                                font-size: 0.675em;
                                color: #878787;
                            }
                        }
                    }
                }
                .total {
                    width: 100%;
                    float: left;
                    border-top: 1px solid #d7d7d7;
                    padding: 12px 15px;
                    font-size: 0.875em;
                    .order-on {
                        float: left;
                        span {
                            color: #878787;
                            margin-right: 7px;
                        }
                    }
                    .total-order {
                        float: right;
                        span {
                            color: #878787;
                            margin-right: 7px;
                        }
                    }
                }
            }
        }
        .coupon-list .coupon {
            width: 100%;
            margin: 20px 0 10px;
            background: #fff;
            border: 1px solid #e2e3e4;
            float: left;
            font-size: 0.875em;
            .offer-detail {
                padding: 12px 15px;
                width: 100%;
                float: left;
                .offer-section {
                    width: 80px;
                    float: left;
                    text-align: center;
                    padding: 2px 0;
                    color: #0bc6a0;
                    font-weight: 900;
                    font-size: 1.4em;
                    border-right: 1px solid #d7d7d7;
                    height: 100%;
                }
                .offer-desc {
                    padding-left: 90px;
                    color: #7e818c;
                    .tax-detail{
                        font-size: 0.675em;
                    }
                    & > div{
                        margin:2px 0;
                    }
                    span {
                        font-weight: 900;
                        padding: 0 5px 0 2px;
                    }
                }
            }
            .offer-expire {
                width: calc(100% - 30px);
                float: left;
                border-top: 1px solid #d7d7d7;
                margin: 0px 15px;
                padding: 12px 0;
                span {
                    font-weight: 900;
                    padding: 0 5px 0 2px;
                }
            }
        }
    }
}