.check-fake-box {
    position: relative;
    display: inline-block;
    input[type="checkbox"] {
        visibility: hidden;
        margin-right: 5px;
    }
    input[type="checkbox"]~div.check-fake {
        background: #FFF;
        position: absolute;
        top: 1px;
        left: 0;
        width: 15px;
        height: 15px;
        border: 1px solid #CCC;
        display: inline-block;
    }
    input[type="checkbox"]~div.check-fake.checked {
        display: none;
        text-align: center;
        border-color: #28a745;
        i {
            color: #28a745;
            position: relative;
            top: -1px;
            display: block;
            left: 2px;
            font-size: 1.375em;
            border-color: #28a745;
        }
    }
    input[type="checkbox"]:checked~div.check-fake.checked {
        display: inline-block;
    }
    input[type="checkbox"]:checked~div.check-fake.unchecked {
        display: none;
    }
    input[type="checkbox"]:hover~div.check-fake {
        border-color: #999;
    }
}

