.box-head {
    text-align: center;
    margin: 0px 0 30px;
}

.box-head h2 {
    font-size: 3em;
    color: #394048;
    font-family: 'Niconne', cursive;
}

.box-head h2:after {
    content: " ";
    height: 2px;
    background: $primary;
    display: block;
    max-width: 50px;
    margin: 10px auto 0;
}

.box-head h4 {
    font-size: 1.175em;
    color: #750500;
    font-style: italic;
    margin-top: 15px;
}

.light {
    background: rgba(0, 0, 0, 0.4) !important;
}

.fixed-top {
    background: #fff;
}

.ellipsis {
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}

.section-bg {
    padding:30px 0 50px 0;
    background-size: 100px 100px;
    background-position: center 10px;
    background-repeat: no-repeat;
}

.section-bg.section-dark {
    background-image: url('./assets/images/om-light.svg')
}

.section-bg.section-light {
    background-image: url('./assets/images/om-dark.svg');
}

.rotate-45 {
    transform: rotate(45deg);
}

.bg-img {
    background-repeat: no-repeat;
    background-size: 100% !important;
    background-position: center center;
	@include animation();
    &:hover {
        background-size: 120% !important;
    }
}

.small-bg-img {
    background-repeat: no-repeat;
    background-size: cover !important;
    background-position: center center;
	@include animation();
    &:hover {
       transform: scale(1.1);
    }
}