.user-rating {
    unicode-bidi: bidi-override;
    direction: rtl;
    display: inline-block;
    margin-left: 10px;
}

.user-rating>span {
    display: inline-block;
    position: relative;
    font-size: 2.4em;
}

.user-rating>span:hover:before,
.user-rating>span:hover~span:before,
.user-rating>span.filled:before,
.user-rating>span.filled~span:before {
    content: "\2605";
    color: gold;
    position: absolute;
}

/*Near By Temples*/

ul.gallery {
    overflow-y: auto;
    max-height: 500px;
    li {
        float: left;
        margin-right: 10px;
        margin-bottom: 10px;
        width: calc(20% - 10px);
        overflow: hidden;
        height: 120px;
        &:last-child {
            padding-right: 0;
        }
        a {
            display: block;
            height: 120px;
            position: relative;
            border-radius: 3px;
            &.more {
                background: #DDD;
                border: 1px solid #EEE;
                font-size: 1.250em;
                text-align: center;
                vertical-align: center;
                width: 100%;
                padding: 35px 0;
                i {
                    font-size: 1.5em;
                    display: block;
                }
            }
        }
        h5 {
            position: absolute;
            bottom: 0;
            left: 0;
            right: 0;
            background: rgba(0, 0, 0, 0.7);
            padding: 5px;
            color: #FFF;
        }
    }
}

.singlePage {
    .sidebarSection {
        h4 {
            font-size: 1em;
            color: $primary;
            text-transform: none;
            border-bottom: 1px solid #ddd;
            padding-bottom: 10px;
            font-weight: 700;
            margin-bottom: 5px;
        }
        .near-by {
            background: $body;
            margin-top: 30px;
            padding: 10px;
            ul {
                height: 300px;
                overflow-y: auto;
                padding: 0;
                margin: 0;
                list-style: none;
            }
        }
        .special-event {
            .item {
                color: #000;
                padding: 0px;
                .tile {
                    -webkit-box-shadow: none !important;
                    box-shadow: none !important;
                    .img {
                        width: 100%;
                        height: 150px;
                    }
                    .header {
                        width: 100%;
                        padding: 5px 0;
                        .name {
                            margin-left: 75px;
                            .txt {
                                font-size: 0.875em;
                                @include ellipsis();
                            }
                            .eventName {
                                color: #333;
                                font-size: 1em;
                                text-align: center;
                                @include ellipsis();
                                font-weight: bold;
                            }
                            .distance-from {
                                float: right;
                                font-size: 0.875em;
                                color: #666;
                                i {
                                    font-size: 0.875em;
                                    color: #990000;
                                }
                            }
                        }
                    }
                }
            }
            .leftRs,
            .rightRs {
                display: none !important;
            }
        }
    }
    .mainSection {
        .mainSectionContent {
            &.temple-detail {
                .banner-head {
                    background: $body;
                    padding: 10px;
                    float: left;
                    width: 100%;
                }
                .devotes {
                    float: right;
                    font-size: 0.875em;
                }
                .summary {
                    font-size: 0.775em;
                    color: $primary_black;
                }
                .open-hour {
                    margin: 5px 2%;
                    float: left;
                    width: 96%;
                    color: $primary;
                    &>div {
                        float: left;
                        width: 28%;
                        margin-left: 5.33%;
                        i {
                            display: inline-block;
                        }
                        div {
                            display: inline-block;
                            margin-left: 10px;
                            h4 {
                                margin: 10px 0 0;
                                font-weight: 900;
                                font-size: 1em;
                            }
                            span {
                                color: #000;
                                font-size: 0.675em;
                                display: block;
                            }
                        }
                    }
                }
                h1 {
                    .connectivity {
                        float: right;
                        color: $gray_dark;
                        position: relative;
                        .tooltip {
                            min-width: 200px;
                        }
                    }
                    i {
                        margin-right: 5px;
                    }
                }
                .temple-details-section {
                    margin-top: 20px;
                    font-size: 0.875em;
                    float: left;
                    width: 100%;
                    ul.nav-tabs {
                        border: none;
                        background: $body;
                        padding: 10px;
                        a.nav-link {
                            border: none;
                            font-size: 1rem;
                            &.active {
                                border-bottom: 3px solid $primary;
                                font-weight: bold;
                            }
                        }
                    }
                    .detail-history {
                        position: relative;
                        div.detail {
                            height: 250px;
                            overflow: hidden;
                        }
                        &:after {
                            content: " ";
                            display: block;
                            height: 100px;
                            @include linearGradient(rgba(255, 255, 255, 0.2), rgba(255, 255, 255, 1));
                            position: absolute;
                            bottom: 22px;
                            left: 0;
                            right: 0;
                            z-index: 1;
                        }
                        .more {
                            position: absolute;
                            bottom: 0;
                            left: 0;
                            right: 0;
                            padding: 3px;
                            background: #DDD;
                            color: #000;
                            font-size: 0.675rem;
                            text-align: center;
                            display: block;
                            z-index: 2;
                            height: 22px;
                            cursor: pointer;
                            &:hover {
                                background: #CCC;
                            }
                        }
                        &.more-content {
                            div.detail {
                                overflow: auto;
                                height: 450px;
                            }
                            &:after {
                                position: relative;
                                height: 22px;
                                bottom: 0px;
                            }
                        }
                    }
                    .pooja-services {
                        .puja-img {
                            height: 142px;
                            background-position: center;
                        }
                    }
                    .temple-needs {
                        ul {
                            li {
                                float: left;
                                width: 33.33333%;
                                font-size: 0.850rem;
                                label {
                                    font-weight: bold;
                                }
                                i {
                                    font-size: 0.750rem;
                                }
                            }
                        }
                    }
                    .donation {
                        width: 100%;
                        float: left;
                        font-size: 0.875em;
                        ul {
                            width: 100%;
                            float: left;
                            padding: 0px;
                            border-bottom: 1px solid #ddd;
                            li {
                                float: right;
                                padding: 5px 8px;
                                background-color: #dedede;
                                margin-right: 3px;
                                &.active {
                                    background-color: #fff;
                                    border-left: 1px solid #ddd;
                                    border-top: 1px solid #007bff;
                                    border-right: 1px solid #ddd;
                                }
                                a.nav-link {
                                    padding: 0px;
                                    font-size: 1.1em;
                                    &.active {
                                        border-bottom: none;
                                        color: #007bff;
                                    }
                                }
                            }
                        }
                        .tab-content {
                            float: left;
                            border: 1px solid #ddd;
                            height: 500px;
                            overflow-y: auto;
                            width: 100%;
                            border-top: none;
                            .tab-pane {
                                .item {
                                    border: 1px solid #ccc;
                                    padding: 5px;
                                    margin: 7px;
                                    float: left;
                                    width: calc(100% - 14px);
                                    .img {
                                        float: left;
                                        width: 100px;
                                        height: 100px;
                                        background-size: cover;
                                        background-position: center;
                                    }
                                    .detail {
                                        padding-left: 110px;
                                        font-size: 1rem;
                                        h5 {
                                            font-size: 1rem;
                                            font-weight: 900;
                                        }
                                        .txt {
                                            font-size: 0.775em;
                                        }
                                        .donateMoney {
                                            font-size: 0.775em;
                                            display: block;
                                            color: #000;
                                            b {
                                                color: #007bff;
                                            }
                                        }
                                        .donate-now {
                                            font-size: 0.875em;
                                            float: left;
                                            text-align: right;
                                            width: 100%;
                                            .calc {
                                                display: inline-block;
                                                padding-right: 10px;
                                                i {
                                                    padding: 0 5px;
                                                    font-size: 0.875em;
                                                    &.fa-minus-circle,
                                                    &.fa-plus-circle {
                                                        font-size: 1.2em;
                                                        color: $primary;
                                                        cursor: pointer;
                                                    }
                                                }
                                            }
                                        }
                                    }
                                }
                            }
                        }
                    }
                    .temple-detail-experts-section {
                        .expert-list {
                            &>div.pandit>div {
                                width: 31.5%;
                                float: left;
                                margin: 7px 2.75% 7px 0;
                                border: 1px solid #bbb;
                                border-radius: 4px;
                                &:nth-child(3n) {
                                    margin-right: 0px;
                                }
                                .img {
                                    width: 100%;
                                    height: 180px;
                                }
                                .meta {
                                    text-align: center;
                                    padding: 10px;
                                    h4 {
                                        margin-bottom: 0px;
                                    }
                                }
                            }
                        }
                    }
                    .poojas {
                        &.inner-section {
                            height: 500px;
                            overflow-y: auto;
                        }
                        .pooja-item {
                            width: 33.333333333333336%;
                            display: inline-block;
                            white-space: initial;
                            padding: 10px;
                            box-sizing: border-box;
                            vertical-align: top;
                            .tile {
                                box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
                                .puja-img {
                                    overflow: hidden;
                                    width: 100%;
                                    &>div {
                                        width: 100%;
                                        height: 142px
                                    }
                                }
                            }
                        }
                    }
                    .temple-detail-events {
                        .event-list {
                            background: #EEE;
                            width: 31%;
                            display: inline-block;
                            margin-bottom: 10px;
                            margin: 5px 1.1666%;
                            border: 1px solid #ccc;
                            vertical-align: top;
                            position: relative;
                            .img {
                                overflow: hidden;
                                width: 100%;
                                height: 180px;
                                &>div {
                                    width: 100%;
                                    height: 180px;
                                }
                            }
                            .meta {
                                text-align: center;
                                padding: 10px;
                                h4 {
                                    margin-bottom: 0px;
                                    font-size: 1em;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}

.comment-section {
    float: left;
    .write-review {
        font-size: 0.725em;
        .user-img {
            float: left;
            width: 80px;
            img {
                max-width: 60px;
                height: auto;
                border-radius: 50%;
            }
        }
        .review-txt {
            float: left;
            width: calc(100% - 80px);
            position: relative;
            input[type='text'] {
                display: inline-block;
                border: 1px solid #ced4da;
                border-radius: .25rem;
                font-size: 1.1em;
                line-height: 1.5;
                width: 60%;
                padding: .375rem .75rem;
                margin-bottom: 5px;
            }
            textarea {
                resize: none;
                font-size: 1.1em;
            }
            .count {
                font-size: 0.875em;
                position: absolute;
                bottom: 2px;
                right: 5px;
            }
        }
        .publish {
            margin-top: 10px;
            float: right;
        }
    }
    .review-section {
        font-size: 0.875em;
        .review {
            background: #EEE;
            padding: 10px;
            margin: 5px 0;
            .user-img {
                width: 50px;
                border-radius: 50%;
                height: 50px;
                margin-right: 10px;
                float: left;
                background-repeat: no-repeat;
                background-size: cover;
                background-position: center center;
            }
            .title {
                font-weight: 900;
                padding: 5px 10px;
                width: 100%;
                float: left;
            }
            .location {
                display: inline-block;
            }
            .rating {
                width: auto;
            }
            .review-txt {
                padding-left: 10px;
            }
        }
    }
}

.event-date-calender {
    color: #000;
    border: 1px solid #a75959;
    float: left;
    text-align: center;
    span {
        display: block;
    }
    span.weekday {
        font-size: 0.625rem;
        background: #990000;
        color: #FFF;
        padding: 2px;
        text-transform: uppercase;
    }
    .day-month {
        padding: 5px;
        span {
            &.day {
                font-size: 1.125em;
                display: block;
                float: left;
                line-height: 1em;
                font-weight: bold;
            }
        }
    }
    .month-year {
        padding: 0px 3px;
        float: right;
        span.month {
            font-size: 0.675em;
            text-transform: uppercase;
            line-height: 0.6750em;
            margin-bottom: 3px;
        }
        span.year {
            font-size: 0.675em;
            line-height: 0.6750em;
        }
    }
}

/*Listing*/

.temple-donations,
.pandit-listing {
    padding: 20px 0;
    .filterSection {
        border: 1px solid #DDD;
        width: 200px;
        float: left;
        padding: 15px;
        background: #FFF;
        border-radius: 5px;
        h4 {
            padding-bottom: 10px;
            margin-bottom: 10px;
            border-bottom: 1px solid #DDD;
            font-weight: bold;
        }
        .filterBox {
            h5 {
                text-transform: uppercase;
                margin-top: 10px;
                font-weight: bold;
            }
            .range {
                display: inline-block;
                font-size: 0.675em;
                float: right;
                color: #484848;
                margin-top: 17px;
            }
            input[type="range"] {
                width: 120px
            }
            .rating {
                label {
                    left: -140px;
                    top: -5px;
                }
            }
            input[type="text"] {
                display: block;
                width: 100%;
                margin: 10px 0;
                padding: 4px 6px;
                font-size: 0.875em;
                line-height: 1;
                color: #495057;
                background-color: #fff;
                background-clip: padding-box;
                border: 1px solid #ced4da;
                border-radius: .25rem;
                transition: border-color .15s ease-in-out, box-shadow .15s ease-in-out;
            }
            .filterList {
                list-style: none;
                padding: 0;
                margin: 5px 0 10px;
                padding-bottom: 15px;
                border-bottom: 1px solid #DDD;
                max-height: 180px;
                overflow-y: auto;
                @include scrollBar("#666");
                li {
                    margin-bottom: 3px;
                    label {
                        user-select: none;
                        cursor: pointer;
                        margin: 0;
                        display: block;
                        font-size: 0.725em;
                    }
                }
            }
        }
    }
    .across-india {
        margin: 0 -5px;
    }
    .main {
        .pandit-list {
            .pandit {
                margin: 10px 0;
                padding: 24px;
                background: #fff;
                border-radius: 4px;
                @include boxShadow(0 2px 5px 0 rgba(0, 0, 0, 0.16));
                float: left;
                width: 100%;
                &:first-child {
                    margin: 0 0 10px 0;
                }
                .img {
                    width: 220px;
                    height: 150px;
                    background-size: cover;
                    background-repeat: no-repeat;
                    float: left;
                    border-radius: 4px;
                    border: 1px solid #d2d3d4;
                }
                .desc {
                    padding-left: 240px;
                    font-size: 0.875em;
                    .pandit-name {
                        width: 60%;
                        float: left;
                        padding-right: 20px;
                        h3 a {
                            color: $primary;
                        }
                        .location {
                            margin: 5px 0;
                        }
                        .about {
                            text-align: left;
                            -webkit-line-clamp: 2;
                            -webkit-box-orient: vertical;
                            overflow: hidden;
                            text-overflow: ellipsis;
                            display: -webkit-box;
                            height: 40px;
                        }
                        .services {
                            font-size: 0.875em;
                            margin-top: 7px;
                            color: #007bff;
                        }
                    }
                    .pooja-type {
                        width: 40%;
                        float: left;
                        border-left: 1px solid #d2d3d4;
                        min-height: 150px;
                        padding-left: 20px;
                        .rating {
                            float: left;
                            text-align: left;
                            margin-bottom: 10px;
                            width: 100px;
                        }
                        p {
                            font-size: 0.875em;
                        }
                        .pooja-modes {
                            clear: both;
                            height: 90px;
                            font-size: 0.875em;
                            ul {
                                list-style: none;
                                i {
                                    color: #00a680;
                                    font-size: 1.2em;
                                    padding-right: 7px;
                                }
                            }
                        }
                        .book-pandit {
                            text-align: right;
                        }
                    }
                }
            }
        }
        padding-left: 220px;
        .contentSection {
            float: left;
            background: #FFF;
            border-radius: 5px;
            width: calc(60% - 20px);
        }
        .sidebarSection {
            float: right;
            width: 40%;
            background: #FFF;
            border-radius: 5px;
            padding: 15px;
        }
        .contentSection {
            .temple-list {
                width: 100%;
                margin: 8px 0;
                border: 1px solid #e2e3e4;
                background: #fff;
                position: relative;
                .desc {
                    padding-left: 130px;
                    .services {
                        font-size: 0.675em;
                        color: #bf2d26;
                    }
                    .badge-success {
                        color: #666666;
                        background-color: #d4f1ff;
                    }
                }
                .details {
                    padding: 15px;
                }
                .img {
                    width: 110px;
                    height: 105px;
                    margin-right: 10px;
                    border-radius: 5px;
                    float: left;
                    overflow: hidden;
                    &>div {
                        width: 100%;
                        height: 100%;
                    }
                }
                .cart-footer {
                    width: 100%;
                    border-top: 1px solid #e2e3e4;
                    &>a {
                        width: 32%;
                        display: inline-block;
                        text-align: center;
                        -webkit-box-pack: center;
                        -moz-box-pack: center;
                        -ms-flex-pack: center;
                        justify-content: center;
                        border-left: 1px solid #e2e3e4;
                        padding: 10px 0;
                        & i {
                            margin-right: 5px;
                        }
                    }
                }
                .address {
                    font-size: 0.725em;
                    color: #AAA;
                }
                .needs {
                    border-top: 1px solid #e2e3e4;
                    width: 96%;
                    margin: 0px 2%;
                    font-size: 0.725em;
                    color: #AAA;
                    clear: both;
                    padding: 10px;
                    label {
                        padding-right: 7px;
                        font-weight: 900;
                        color: #333;
                    }
                    span {
                        color: #666;
                    }
                    li {
                        line-height: 0.725em;
                        width: 33.3%;
                        float: left;
                    }
                }
                .certified {
                    position: absolute;
                    background: url(/assets/images/certified.png);
                    background-size: 90%;
                    background-position: center;
                    width: 106px;
                    height: 78px;
                    background-repeat: no-repeat;
                    top: 0px;
                    right: -5px;
                }
            }
        }
    }
    .trending {
        font-size: 0.875em;
        margin-top: 7px;
        span:first-child {
            font-weight: 900;
            padding-right: 10px;
        }
    }
}

.pandit-detail-section {
    .left-pandit {
        float: left;
    }
    .right-pandit {
        float: right;
    }
    .left-pandit a:before,
    .right-pandit a:before {
        content: "";
        background-image: url("/assets/images/pandit-ji.png");
        min-height: 80px;
        background-position: right;
        background-repeat: no-repeat;
        background-size: 80%;
        display: inline-block;
        width: 140px;
        cursor: default;
        opacity: 0.7;
    }
    .left-pandit a:before {
        transform: scaleX(-1);
    }
    .trust {
        position: relative;
        margin: 0 auto;
        text-align: center;
        width: 900px;
        height: 40px;
        ul {
            position: absolute;
            width: 900px;
            border: 1px solid #E0E0E0;
            padding: 16px 8px;
            top: -100px;
            left: 132px;
            background: #fff;
            list-style: none;
            li {
                float: left;
                width: 220px;
            }
        }
    }
    .overview {
        & div.section {
            padding: 0 20px 50px;
            margin-top: -50px;
        }
        h4 {
            font-size: 1.8em;
            margin: 0.675em 0;
            font-weight: bold;
            padding-bottom: 10px;
            font-family: arial;
            border-bottom: 1px solid #E0E0E0;
