                        float: left;
                        padding: 10px 24px 0;
                        color: #28a745;
                        &.awards {
                            color: gold;
                        }
                    }
                }
            }
            .rating-pandit {
                width: 50%;
                float: left;
                border-right: 1px solid #E0E0E0;
                height: 254px;
                text-align: left;
                padding-right: 1%;
                &>.rating {
                    width: auto;
                    float: left;
                    padding-right: 10px;
                    font-size: 1.8em;
                    padding-bottom: 10px;
                }
                .review-line {
                    font-weight: bold;
                    padding-bottom: 10px;
                    font-size: 1.4em;
                }
                ul {
                    list-style: none;
                    li {
                        label {
                            width: 100px;
                            margin-bottom: 0;
                        }
                        .progress {
                            width: 180px;
                            display: inline-block;
                        }
                        span {
                            font-size: 0.875em;
                        }
                    }
                }
                .user-rating {
                    .user {
                        margin: 10px 0;
                        float: left;
                        width: 100%;
                        .left-section {
                            width: 96px;
                            float: left;
                            text-align: center;
                            .img {
                                background-size: cover;
                                background-position: center;
                                background-repeat: no-repeat;
                                width: 48px;
                                height: 48px;
                                border-radius: 50%;
                                margin: 0 24px;
                            }
                            .user-name {
                                font-size: 0.775em;
                            }
                        }
                        .review-detail {
                            padding-left: 96px;
                            font-size: 0.875em;
                            .rating {
                                width: auto;
                                float: left;
                                padding-right: 7px;
                            }
