                border-width: 1px;
                border-style: solid;
                border-color: #e5e5e5;
                position: relative;
                z-index: 2;
                h5 {
                    font-size: 1.2em;
                    margin: 0.875em 0;
                    font-weight: bold;
                    span {
                        color: $primary;
                    }
                }
