                    li {
                        padding: 3px 5px;
                        a {
                            font-size: 0.975em;
                            color: #b18605;
                        }
                    }
                }
            }
            &:after,
            &:before {
                position: absolute;
                bottom: 27px;
                width: 40%;
                height: 36px;
                border-radius: 12px;
                box-shadow: 0 0 16px 12px rgba(0, 0, 0, 0.4);
                content: "";
                z-index: 1;
            }
            &:before {
                left: 28px;
                transform: rotate(-8deg);
                -ms-transform: rotate(-8deg);
                -moz-transform: rotate(-8deg);
                -webkit-transform: rotate(-8deg);
            }
            &:after {
                right: 28px;
                transform: rotate(8deg);
                -ms-transform: rotate(8deg);
                -moz-transform: rotate(8deg);
                -webkit-transform: rotate(8deg);
            }
        }
    }
}

