/*Gallery*/

.main-gallery {
    width: 25%;
    height: 422px;
    float: left;
    background-size: cover;
    background-repeat: no-repeat;
    position: relative;
    background-position: center;
    overflow: hidden;
}

.sub-gallery {
    width: 75%;
    float: left;
}

.sub-gallery .sub-gallery-1 {
    margin-left: 1px;
    float: left;
    width: calc(33.33% - 1px);
    margin-bottom: 1px;
    height: 261px;
    background-size: cover;
    background-repeat: no-repeat;
    position: relative;
    background-position: center;
    overflow: hidden;
}

.sub-gallery .sub-gallery-2 {
    margin-left: 1px;
    float: left;
    width: calc(20% - 1px);
    height: 160px;
    background-size: cover;
    background-repeat: no-repeat;
    position: relative;
    overflow: hidden;
    background-position: center;
}

.gallery-section .description {
    width: 100%;
    color: #FFF;
    padding: 12px;
    text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.6);
    position: absolute;
    bottom: -40px;
    left: 0px;
    -moz-transition: all ease 1s;
    transition: all 1s ease;
    -webkit-transition: all 500ms ease;
    -ms-transition: all 500ms ease;
    -o-transition: all 500ms ease;
    background: transparent 0%;
    background: -moz-linear-gradient(top, transparent 0% 0%, rgba(0, 0, 0, 0.8) 100% 100%);
    background: -webkit-gradient(left top, left bottom, color-stop(0%, transparent 0%), color-stop(100%, rgba(0, 0, 0, 0.8) 100%));
    background: -webkit-linear-gradient(top, transparent 0% 0%, rgba(0, 0, 0, 0.8) 100% 100%);
    background: -o-linear-gradient(top, transparent 0% 0%, rgba(0, 0, 0, 0.8) 100% 100%);
    background: -ms-linear-gradient(top, transparent 0% 0%, rgba(0, 0, 0, 0.8) 100% 100%);
    background: linear-gradient(to bottom, transparent 0% 0%, rgba(0, 0, 0, 0.8) 100% 100%);
}

.gallery-section .main-gallery:hover .description,
.gallery-section .sub-gallery-1:hover .description,
.gallery-section .sub-gallery-2:hover .description {
    bottom: 0px;
}

/*Audio Video*/

.video-section {
    width: 60%;
    float: left;
    margin-right: 2%;
}

.video-section video {
    background: #333333;
}

.video-section .item {
    width: 120px !important;
    height: 100px;
    margin-right: 10px;
    border: 1px solid;
}

.video-section .rightRs,
.video-section .leftRs {
    position: absolute;
    top: 50%;
    margin-top: -12px;
}

.video-section .rightRs {
    right: 10px;
}

.video-section .leftRs {
    left: 10px;
}

.video-section .rightRs i,
.video-section .leftRs i {
    color: #FFF;
    text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.5)
}

.audio-section {
    float: left;
    width: 38%;
}

.audio-video h4 {
    font-size: 1.272em;
    color: #1b5694;
    padding-bottom: 15px;
}

.audio-video .audio {
    border: 1px solid #d2d3d4;
    height: 506px;
    overflow-y: auto;
}

.audio-video .audio .audio-img img {
    max-width: 100px;
    border: 1px solid;
}

.audio-video .audio .audio-img {
    float: left;
    margin-right: 10px;
}

.audio-video .audio .audio-box {
    border-bottom: 1px solid #d2d3d4;
    float: left;
    width: 100%;
    padding: 10px;
}

.audio-video .audio .audio-detail {
    float: left;
    width: calc(100% - 110px);
}

.audio-section .audio::-webkit-scrollbar {
    width: 2px;
    background-color: #F5F5F5;
}

.audio-section .audio::-webkit-scrollbar-thumb {
    border-radius: 10px;
    @include boxShadow(inset 0 0 6px rgba(0,
    0,
    0,
    0.3));
    background-color: #e27412;
}

.audio-detail .title,
.audio-detail .date {
    float: left;
    font-size: 0.675em;
    color: #b2b3b5;
    margin: 3px 0 10px
}

.audio-detail .date {
    float: right;
    font-weight: 600;
}

/****** Style Star Rating Widget *****/

.rating {
    direction: rtl;
    text-align: right;
    float: right;
    line-height: 0.75rem;
    width: 100%;
    i {
        color: gold;
        padding: 0 2px;
    }
    input[type=radio] {
        float: left;
    }
}

.section-book-pandit .pandit-list .pandit-box div.pandit h5 {
    display: inline-block;
}

// Footer section
