    background: #111;
    padding: 20px 0;
    border-top: 3px solid #bf2d26;
    z-index: 1;
    color: #fff;
    font-size: 0.575em;
    .download-section>div,
    .religios-india-footer-section {
        width: 33%;
        float: left;
    }
    .download-section>div img {
        margin: 10px 10px 0 0;
    }
    .subscribe input {
        width: 70%;
        margin: 10px 2% 0 0;
        float: left;
    }
    .subscribe a,
    .section-footer .connect>div {
        margin-top: 10px
    }
    .section-footer {
        hr {
            border-top: 1px solid #5f5050;
        }
        .connect>div i {
            padding-right: 15px;
            cursor: pointer;
        }
    }
    .religios-india-footer-section {
